import { motion } from "framer-motion";
import Image from "next/image";
import React from "react";

export const AnimatedWildBoar = () => {
  return (
    <motion.div
      initial={{ x: "-100%", y: 0 }}
      animate={{
        x: "100%",
        y: [0, -10, 0],
      }}
      transition={{
        type: "tween",
        ease: "easeInOut",
        duration: 2,
        repeat: Infinity,
        repeatType: "loop",
      }}
      className="wild-boar-container"
    >
      <Image src="/wild-boar.png" alt="Wild Boar" width={200} height={200} />
    </motion.div>
  );
};
